body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

h1 {
  font-size: 24px;
  text-align: center;
  color: #333;
}

.qr-scanner-container {
  text-align: center;
  padding: 20px;
}

.qr-scanner-container h2 {
  font-size: 20px;
  margin-bottom: 20px;
  color: #333;
}

.qr-scanner-container video {
  width: 100%;
  max-width: 400px;
  height: auto;
  border: 2px solid #007bff;
  border-radius: 8px;
}

.answerbutton{
  display: block;
  width: 20%;
  padding: 10px;
  margin: 20px auto;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.scannerbutton {
  display: block;
  width: 50%;
  padding: 10px;
  margin: 20px auto;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}


.exchangebutton { 
  display: block;
  width: 50%;
  padding: 10px;
  margin: 20px auto;
  font-size: 16px;
  color: #fff;
  background-color: #1dae06;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #e2e6ea;
}

input[type="text"] {
  display: block;
  width: 100%;
  padding: 10px;
  margin: 20px 0;
  font-size: 16px;
  border: 2px solid #007bff;
  border-radius: 4px;
  box-sizing: border-box;
}

.quiz-question {
  font-size: 18px;
  margin-bottom: 20px;
}

.quiz-options {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1px;
  list-style: none;
  padding: 0;
}

.quiz-options li {
  margin-bottom: 10px;
}

.quiz-options input[type="radio"] {
  display: none; /* デフォルトのラジオボタンを非表示にする */
}

.quiz-options input[type="radio"] + label {
  display: inline-block;
  padding: 10px 20px;
  border: 2px solid #007bff;
  border-radius: 4px;
  cursor: pointer;
  background-color: #fff;
  color: #007bff;
  transition: background-color 0.3s, color 0.3s;
}

.quiz-options input[type="radio"]:checked + label {
  background-color: #007bff;
  color: #fff;
}

.score {
  font-size: 20px;
  text-align: center;
  color: #333;
}